.header-container {
  background-image: url("../assets/images/background-head.png");
  margin-left: -2em;
  margin-top: -1.3em;
  box-shadow: 0 0 1em 0 rgba(50, 50, 50, 0.8); }

header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white; }

.header_li {
  display: flex;
  flex-direction: column; }

header img {
  height: 4.5em;
  width: 3.9em;
  padding-top: 2.5em;
  padding-bottom: 1.5em;
  padding-left: 10em; }

.li {
  font-size: 0.9em;
  font-family: Montserrat, serif;
  padding-top: 1em; }

.li:hover {
  border-bottom: 2px solid white;
  transition: all 0.1s; }

link {
  text-decoration: none; }

.button-container {
  padding-top: 0.5em;
  margin-right: 8em;
  margin-top: 0.1em; }

button {
  background: #EEF4FB;
  border: 0.1em solid #D2E3F3;
  border-radius: 10em;
  margin-top: 0.6em;
  width: 8.5em;
  height: 2.6em; }

.signIn {
  border: 2px solid gold;
  background: #fdc526;
  color: white; }

.signIn:hover {
  border: 0; }

.signIn:hover {
  cursor: pointer; }

.registration {
  border: 1px solid black;
  background-color: Transparent;
  cursor: pointer; }

.registration:hover {
  border: 1px solid gray; }

.registration:hover {
  cursor: pointer; }
